import React, { useState } from "react";
import "./EditTodo.css";
import toast from "react-hot-toast";
import TodoService from "../Services/TodoService";
const EditTodo = ({ task, setShowModal ,getUserTask }) => {
  const [title, setTitle] = useState(task?.title);
  const [discription, setDiscription] = useState(task?.discription);
  const [isCompleted, setIsCompleted] = useState(task?.isCompleted);

  const closeHandler = () => {
    setShowModal(false);
  };
  const handleSelectChange = (e) => {
    setIsCompleted(e.target.value);
  };

  const id = task?._id;
  // handleSubmit
  const submitHandler = async () => {
    try {
      const userData = JSON.parse(localStorage.getItem("todoapp"));
      const createdBy = userData && userData.user.id;
      const data = { title, discription, createdBy , isCompleted };
      if (!title || !discription) {
        return toast.error("please provide title or discription");
      }
      await TodoService.updateTodo(id,data);
      setShowModal(false);
      toast.success("Task Updated Successfully");
      setTitle("");
      setDiscription("");
      getUserTask();
    } catch (error) {
      console.log(error);
      toast.error(error);
    }
  };

  return (
    <>
      {task && (
        <div
          className="modal show"
          tabIndex="-1"
          role="dialog"
          style={{ display: "block", backgroundColor: "rgba(0,0,0,0.5)" }}
        >
          <div className="modal-dialog" role="document">
            <div className="modal-content">
              {/* Header */}
              <div className="modal-header">
                <h5 className="modal-title">Update Your Task</h5>
                <button
                  type="button"
                  className="btn-close"
                  aria-label="Close"
                  onClick={closeHandler}
                ></button>
              </div>

              {/* Body */}
              <div className="modal-body">
                <div className="mb-3">
                  <label className="form-label">Title</label>
                  <input
                    type="text"
                    className="form-control"
                    value={title}
                    onChange={(e) => setTitle(e.target.value)}
                  />
                </div>
                <div className="form-floating">
                  <textarea
                    className="form-control"
                    id="floatingTextarea"
                    placeholder="Add your discription"
                    value={discription}
                    onChange={(e) => setDiscription(e.target.value)}
                  ></textarea>
                  <label htmlFor="floatingTextarea">Description</label>
                </div>
                <div className="my-3">
                  <select className="form-select" onClick={handleSelectChange}>
                    <option selected>Select Option</option>
                    <option value={true}>Completed</option>
                    <option value={false}>Incomplete</option>
                  </select>
                </div>
              </div>

              {/* Footer */}
              <div className="modal-footer">
                <button
                  onClick={closeHandler}
                  type="button"
                  className="btn btn-secondary"
                >
                  Close
                </button>
                <button
                  type="button"
                  className="btn btn-primary"
                  onClick={submitHandler}
                >
                  Create
                </button>
              </div>
            </div>
          </div>
        </div>
      )}
    </>
  );
};

export default EditTodo;
