import React, { useState } from "react";
import EditTodo from "../EditTodo";
import toast from "react-hot-toast";
import TodoService from "../../Services/TodoService";

const Card = ({ allTask, getUserTask, setAllTask }) => {
  const [showModal, setShowModal] = useState(false);

  // edit handler
  const editHandler = () => {
    setShowModal(true);
  };

  // delete handle
  const handleDelete = async (id) => {
    try {
      await TodoService.deleteTodo(id);
      setAllTask((prev) => prev.filter((task) => task._id !== id));
      toast.success("Task deleted successfully");
    } catch (error) {
      console.log("Delete Error:", error);
      toast.error(error.response?.data?.message || error.message);
    }
  };

  return (
    <>
      {allTask?.map((task, index) => (
        <>
          <div
            className="card border-primary "
            style={{ maxWidth: "18rem" }}
            key={index}
          >
            {/* header */}
            <div className="card-header">
              <div className="chead">
                <h6>{task?.title.substring(0, 10)}</h6>
                <h6
                  className={
                    task?.isCompleted === true ? "task-cmp" : "task-inc"
                  }
                >
                  {task?.isCompleted === true ? "Completed" : "incompleted"}
                </h6>
              </div>
            </div>
            {/* body */}
            <div className="card-body">
              <h6 style={{ fontWeight: "bold" }}>{task?.title}</h6>
              <p className="card-text">{task?.discription}</p>{" "}
            </div>
            <h6 className="DateFormate">{task?.createdAt.substring(0, 10)} </h6>
            {/* footer */}
            <div className="card-footer border-primary">
              <button
                className="btn btn-warning "
                title="Edit Task"
                onClick={editHandler}
              >
                <i className="fa-solid fa-pen-to-square"></i>
              </button>
              <button
                className="btn btn-danger ms-2 "
                title="Delete Task"
                onClick={() => handleDelete(task?._id)}
              >
                <i className="fa-solid  fa-trash"></i>
              </button>
            </div>
          </div>
          <div>
            {showModal && (
              <EditTodo
                task={task}
                setShowModal={setShowModal}
                getUserTask={getUserTask}
              />
            )}
          </div>
        </>
      ))}
    </>
  );
};

export default Card;
