import axios from "axios";

// get user token
const user = JSON.parse(localStorage.getItem("todoapp"));

//  FIXED: safe check lagaya hai, agar user null ho to crash na ho
if (user?.token) {
  axios.defaults.headers.common["Authorization"] = `Bearer ${user.token}`;
} else {
  // agar token nahi mila to header hata diya (warna undefined token bhejega)
  delete axios.defaults.headers.common["Authorization"];
}

// create todo service
const createTodo = (data) => {
  return axios.post("/api/v1/todo/create", data);
};

// Get all todo service
const getAllTodo = (id) => {
  return axios.post(`/api/v1/todo/getAll/${id}`);
};

// update todo service
const updateTodo = (id, data) => {
  return axios.patch(`/api/v1/todo/update/${id}`, data);
};

//  FIXED: deleteTodo function return kara diya (pehle empty tha)
const deleteTodo = (id) => {
  return axios.delete(`/api/v1/todo/delete/${id}`);
};

const TodoService = { createTodo, getAllTodo, updateTodo, deleteTodo };

export default TodoService;

