import React from 'react'
import { Link } from 'react-router-dom';
import Hero from '../../assets/images/image.png'
import "./Landing.css"
const Landing = () => {
  return (
    <div className='hero'>
        <div className='intor-text'>
          <h1>
            <span className='tagline1'>Organize wrok and life</span>
            <span className='tagline2'>finally</span>
          </h1>
          <p>
            type just anything into the tsked field and TodoList <br/>
            will help you to organize your work and life.
          </p>
          <Link className='btn red' to="/register">Register Now!</Link>
          <Link className='btn blue' to="/login">Login</Link>
        </div>
        <div className="">
          <img src={Hero} alt="hero Image" width={'100%'} height="500" />
        </div>
    </div>
  )
}

export default Landing