import React, { useEffect, useState } from "react";
import Navbar from "../../Components/Layout/Navbar";
import PopModal from "../../Components/Layout/PopModal";
import TodoService from "../../Services/TodoService";
import { get, set } from "mongoose";
import Card from "../../Components/Card/Card";
import Spinner from "../../Components/Layout/Spinner";
const HomePage = () => {
  //use States
  const [showModal, setShowModal] = useState(false);
  const [searchQuery, setSearchQuery] = useState("");
  const [loading, setLoading] = useState(false);
  const [title, setTitle] = useState("");
  const [discription, setDiscription] = useState("");
  const [allTask, setAllTask] = useState([]);

  //------------------------
  //handle modal
  const openModalHandler = () => {
    setShowModal(true);
  };
  //------------------------
  //handle search
  const handelSearch = (e) => {
    const query = e.target.value;
    let filterList = allTask?.filter((item) =>
      item.title.toLowerCase().match(query.toLowerCase())
    );
    setSearchQuery(query);
    if (query && filterList.length > 0) {
      setAllTask(filterList && filterList);
    } else {
      getUserTask();
    }
  };

  // get user
  const userData = JSON.parse(localStorage.getItem("todoapp"));
  const id = userData && userData.user.id;
  const getUserTask = async () => {
    setLoading(true);
    try {
      const { data } = await TodoService.getAllTodo(id);
      setLoading(false);
      setAllTask(data?.todos);
    } catch (error) {
      setLoading(false);

      console.log(error);
    }
  };
  //useEffect
  useEffect(() => {
    getUserTask();
  }, []);

  return (
    <>
      {/* -------------Navbar---------- */}
      <Navbar />
      {/* ------------------------ */}
      <div className="container">
        <div className="add-task">
          <h1>Your Task</h1>
          <input
            type="search"
            placeholder="Search your task"
            value={searchQuery}
            onChange={handelSearch}
          />
          <button className="btn btn-primary" onClick={openModalHandler}>
            Create Task <i className="fa-solid fa-plus"></i>
          </button>
        </div>
        {/* -------------Card---------- */}
        <div className="CardContainer">
          {loading ? (
            <Spinner />
          ) : (
            allTask && (
              <Card
                allTask={allTask}
                getUserTask={getUserTask}
                setAllTask={setAllTask}
              />
            )
          )}
        </div>

        {/* ======== modal=========== */}
        <PopModal
          getUserTask={getUserTask}
          showModal={showModal}
          setShowModal={setShowModal}
          title={title}
          setTitle={setTitle}
          discription={discription}
          setDiscription={setDiscription}
        />
      </div>
    </>
  );
};

export default HomePage;
