import React, { useEffect, useState } from "react";
import Navbar from "../.././Components/Layout/Navbar";
import TodoService from "../../Services/TodoService";
import Spinner from "../../Components/Layout/Spinner";
import './TodoList.css'

const TodoList = () => {
  const [todoStatus, setTodoStatus] = useState("");
  const [filterdTask, setFilterTask] = useState([]);
  const [loading, setLoading] = useState(false);
  const [allTask, setAllTask] = useState([]);

  // get user
  const userData = JSON.parse(localStorage.getItem("todoapp"));
  const id = userData && userData.user.id;
  const getUserTask = async () => {
    setLoading(true);
    try {
      const { data } = await TodoService.getAllTodo(id);
      setLoading(false);
      setAllTask(data?.todos);
    } catch (error) {
      setLoading(false);

      console.log(error);
    }
  };
  //useEffect
  useEffect(() => {
    const incomplete = allTask?.filter((item) => item?.isCompleted === false);
    const completed = allTask?.filter((item) => item?.isCompleted === true);
    if (todoStatus === "incomplete") {
      setFilterTask(incomplete);
    } else if (todoStatus === "completed") {
      setFilterTask(completed);
    }
    getUserTask();
  }, [todoStatus]);
  return (
    <>
      <Navbar />
      <div  className="filter-container">
        <h4>Filter Todo by </h4>
        <div className="filter-group">
          <select
            className="form-select"
            onChange={(e) => setTodoStatus(e.target.value)}
          >
            <option selected>Select status</option>
            <option value="incomplete">Incomplete</option>
            <option value="completed">Completed</option>
          </select>
        </div>
      </div>
      {/* ============ */}
      {loading && <Spinner />}
      <div className="MyTodoContainer">
        {filterdTask?.map((task, index) => (
        <>
          <div

            className="card border-primary mb-3 mt-3 "
            style={{ maxWidth: "18rem" }}
            key={index}
          >
            {/* header */}
            <div className="card-header">
              <div className="chead">
                <h6>{task?.title.substring(0, 10)}</h6>
                <h6
                  className={
                    task?.isCompleted === true ? "task-cmp" : "task-inc"
                  }
                >
                  {task?.isCompleted === true ? "Completed" : "incompleted"}
                </h6>
              </div>
            </div>
            {/* body */}
            <div className="card-body">
              <h6 style={{ fontWeight: "bold" }}>{task?.title}</h6>
              <p className="card-text">{task?.discription}</p>{" "}
            </div>
            <h6 className="DateFormate">{task?.createdAt.substring(0, 10)} </h6>
          </div>
          
        </>
      ))}
      </div>
    </>
  );
};

export default TodoList;
